@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
*{margin:0;padding:0;font-family:"Roboto", sans-serif;box-sizing:border-box}body{background-color:#5352ed}.grid{display:grid;grid-template-rows:1fr -webkit-min-content;grid-template-rows:1fr min-content;grid-row-gap:.8em;row-gap:.8em;height:100vh}

.header__search{border:none;outline:none;cursor:pointer;background-color:transparent}.header__title{font-style:none;color:transparent}.header{position:fixed;top:0;z-index:100;width:100%;display:grid;grid-template-columns:-webkit-max-content 1fr;grid-template-columns:max-content 1fr;grid-column-gap:.8em;justify-content:center;align-items:center;padding:.8em;background-color:#1e3799;border-bottom:1px solid #2f3640}.header__title{display:grid;grid-template-columns:repeat(2, -webkit-max-content);grid-template-columns:repeat(2, max-content);grid-column-gap:0.4em;align-items:center}.header__title__icon{font-size:1.5rem;color:#5352ed}.header__title__title{font-size:1.5rem;color:#f1f2f6;transition:color 0.2s ease-in-out}.header__title:hover .header__title__title{color:#5352ed}.header__search{font-size:1.3rem;line-height:1.3rem;color:#f1f2f6;transition:color 0.2s ease-in-out}.header__search:hover{color:#5352ed}

.input__submit{border:none;outline:none;cursor:pointer;background-color:transparent}.input{display:grid;grid-template-columns:1fr -webkit-max-content;grid-template-columns:1fr max-content;grid-column-gap:.8em;align-items:center}.input__input{width:100%;outline:none;font-size:1rem;padding:0.3em 0.8em;background-color:#f1f2f6;border:none;border-radius:40px;color:#2f3640}.input__submit{font-size:1.3rem;color:#f1f2f6;transition:color 0.2s ease-in-out}.input__submit:hover{color:#5352ed}

.items{margin-top:5em}.items{display:grid;justify-self:center;grid-template-rows:-webkit-min-content;grid-template-rows:min-content;grid-auto-rows:1fr;grid-gap:.8em;gap:.8em}@media screen and (max-width: 759px){.items{grid-template-columns:-webkit-min-content;grid-template-columns:min-content}}@media screen and (min-width: 759px) and (max-width: 1140px){.items{grid-template-columns:repeat(2, -webkit-min-content);grid-template-columns:repeat(2, min-content)}}@media screen and (min-width: 1140px){.items{grid-template-columns:repeat(3, -webkit-min-content);grid-template-columns:repeat(3, min-content)}}.items__info{color:#f1f2f6}@media screen and (max-width: 759px){.items__info{grid-column:span 1}}@media screen and (min-width: 759px) and (max-width: 1140px){.items__info{grid-column:span 2}}@media screen and (min-width: 1140px){.items__info{grid-column:span 3}}

.item{font-style:none;color:transparent}.item{background-color:#f1f2f6;display:grid;grid-template-rows:-webkit-min-content 1fr;grid-template-rows:min-content 1fr;grid-row-gap:.8em;row-gap:.8em;border-radius:10px;padding:.8em;box-shadow:0 3px 10px black}.item__thumb{position:relative;top:0;border-radius:10px;transition:top 0.2s ease-in-out}.item__info:before{content:"";display:block;width:100%;height:2px;background:rgba(0,0,0,0.5)}.item__info__title{font-size:1.2rem;color:#2f3640;margin-top:0.4em}.item__info__channel{font-size:1rem;color:rgba(0,0,0,0.5)}.item:hover .item__thumb{top:-5px}

.details__video__des{border:none;outline:none;cursor:pointer;background-color:transparent}.details{margin-top:5em}.details{display:grid;justify-content:center;grid-gap:2em;gap:2em}@media screen and (max-width: 759px){.details{grid-template-rows:repeat(2, -webkit-max-content);grid-template-rows:repeat(2, max-content)}}@media screen and (min-width: 759px) and (max-width: 1140px){.details{grid-template-rows:repeat(2, -webkit-max-content);grid-template-rows:repeat(2, max-content);grid-template-columns:704px}}@media screen and (min-width: 1140px){.details{grid-template-columns:repeat(2, -webkit-min-content);grid-template-columns:repeat(2, min-content);justify-content:center}}.details__video{background-color:#f1f2f6;display:grid;justify-content:center;grid-template-rows:repeat(3, -webkit-min-content);grid-template-rows:repeat(3, min-content);grid-template-columns:-webkit-min-content;grid-template-columns:min-content;grid-row-gap:.8em;row-gap:.8em;padding:.8em;border-radius:10px;box-shadow:0 3px 10px black}@media screen and (min-width: 1140px){.details__video{height:-webkit-min-content;height:-moz-min-content;height:min-content}}@media screen and (max-width: 759px){.details__video iframe,.details__video object,.details__video embed{width:360px;height:202px}}@media screen and (min-width: 759px) and (max-width: 1140px){.details__video iframe,.details__video object,.details__video embed{width:680px;height:382px}}@media screen and (min-width: 1140px){.details__video iframe,.details__video object,.details__video embed{width:720px;height:405px}}.details__video__des{border-radius:5px;transition:all 0.2s ease-in-out}.details__video__des:hover{color:#f1f2f6;background-color:rgba(0,0,0,0.7)}.details__video__info:before{content:"";display:block;width:100%;height:2px;background:rgba(0,0,0,0.5);margin-bottom:0.4em}.details__video__info__title{font-size:1.3rem;color:#2f3640}.details__video__info__channel{font-size:1rem;color:rgba(0,0,0,0.5)}.details .items{margin-top:0em}@media screen and (min-width: 1140px){.details .items{grid-template-columns:-webkit-min-content;grid-template-columns:min-content;overflow:auto}}@media screen and (min-width: 1140px){.details .items__info{grid-column:span 1}}

.footer{border-top:2px solid #2f3640;background-color:black;height:150px;color:#9b9b9b;font-size:0.8rem;display:flex;flex-direction:column;justify-content:center;padding:0 50px}.footer__creator{display:grid;grid-template-columns:130px 1fr}.footer__creator:before{content:"Creators"}.footer__contacts{display:grid;grid-template-columns:130px 1fr}.footer__contacts:before{content:"Contacts"}.footer__contacts__content{display:flex}.footer__policy{display:grid;grid-template-columns:130px 1fr}.footer__policy:before{content:"Policy"}.footer__policy__content{display:flex}

